#footer {
    font-family: 'Roboto Mono', monospace;
    color: rgba(194, 194, 194, 0.902);
    font-size: 0.8rem;
    background-color: rgba(57, 57, 57, 0.34);
    box-shadow: 0 3px 10px rgba(95, 95, 95, 0.5);
    padding: 10px 30px;
    width: auto;
    border-radius: 13px;
    text-align: center;
    box-sizing: border-box;
    bottom: 0;
    position: relative; 
}

footer a {
    text-decoration: none;
    font-weight: bold;
    color: rgb(94, 160, 255);
    margin: 2px;
    transition: color 0.5s;
}

footer a:hover {
    color: rgb(10, 112, 255);
    background-color: pink;
    font-weight: bold;

}

footer span {
    margin: 0 0;
}

#rocketIcon {
    margin-left: 1px;
    margin-right: 1px;
}


@media screen and (max-width: 900px) {
    #footer {
        font-size: 0.70rem;
        line-height: 15px;
    }
}